import * as React from "react"
import { QuoteStyle } from "../../types";
import CategoryCarousel from "../components/CategoryCarousel/CategoryCarousel";
import Layout from "../components/Layout/Layout"
import PageTitle from "../components/PageTitle/PageTitle";
import Quote from "../components/Quote/Quote";
import Centralizer from "../components/structure components/Centralizer/centralizer";



const IndexPage = () => {


  return (
    <Layout>

      <section style={{ position: "relative", top: "30vh", width: "50vw", left: "40vw", height: "40vh" }}>
        <Quote>
          Ci sono sapori e profumi che evocano ricordi in ognuno di noi, ci auguriamo di regalare
          quei momenti di dolcezza ad ognuno di voi.
          </Quote>
      </section>

      <section style={{ position: "relative", left: "0", right: "0", top: "0", marginTop: "100px" }}>
        <CategoryCarousel />
      </section>


      {/* ------------------------------BEIGE SECTION */}
      <section style={{ position: "relative", height: "95vh", marginTop: "-56px", backgroundColor: "rgba(178, 147, 121, 0.2)" }}>
        <div style={{ height: "40vh" }}>
          <Centralizer column>
            <div style={{ width: "70vw" }}>
              <Quote>
                Flaviano nasce dal desiderio di trasmettere lamore e la passione per lartigianalit.
                Siamo artigiani moderni che si pongono lobiettivo di promuovere la tradizione attraverso
                la creativit. Siamo custodi delle bont del nostro territorio.
            </Quote>
            </div>
          </Centralizer>
        </div>

        <div style={{ width: "150px", height: "150px", textAlign: "center", position: "absolute", left: "11vw", bottom: "15vh" }}>
          <Quote style={QuoteStyle.header}>
            P
          </Quote>
        </div>

        <div style={{ width: "150px", height: "150px", textAlign: "center", position: "absolute", right: "25vw", bottom: "5vh" }}>
          <PageTitle>Prodotti</PageTitle>
        </div>

      </section>



      {/* ------------------------------PRODUCTS SECTION */}


      <section style={{ position: "relative", height: "90vh", marginTop: "-56px", backgroundColor: "white" }}>

      </section>



    </Layout>
  )
}

export default IndexPage;


